import { v4 as uuidv4 } from "uuid";
import { Buffer } from "buffer";
import * as dayjs from "dayjs";
export function createUUID() {
  return uuidv4();
}
/**
 *  
 * @param {String} input
 * @param {regExp} regExp
 * @returns { input, regExp, result }
 */
export function checkRegExp(input, regExp) {
  const regex = new RegExp(regExp, "g");
  const result = regex.test(input);
  return { input, regExp, result };
}

/**
 *  
 * @param {String} token
 * @returns json
 */
export function decodeJWT(token) {
  const base64Payload = token.split(".")[1];
  const payload = Buffer.from(base64Payload, "base64").toString();
  return JSON.parse(payload.toString());
}

/**
 * timeStamp Date 
 *  
 * @param {number} timeStamp
 * @returns Date
 */
export function convertUnixTimeStampToDate(timeStamp) {
  return new Date(timeStamp * 1000);
}

/**
 *   
 * @param {String} input
 * @returns { input, regExp, result }
 */
export function validEmail(input) {
  return checkRegExp(input, /^[^\s@]+@[^\s@]+\.[^\s@]+$/);
}

export class _Log {
  static log(level = "info", ...args) {
    const colors = {
      success: "color:green;font-weight:bold",
      error: "color:red;font-weight:bold",
      warn: "color:orange;font-weight:bold",
      info: "font-weight:bold",
      debug: "color:gray;font-weight:bold",
    };
    const now = dayjs().format("MM/DD HH:mm:ss");
    const color = colors[level];
    const _level = level.toUpperCase().padEnd(7, " ");
    console.log(`[${now}] %c${_level}`, color, ...args);
  }

  static success(...args) {
    _Log.log("success", ...args);
  }
  static error(...args) {
    _Log.log("error", ...args);
  }

  static warn(...args) {
    _Log.log("warn", ...args);
  }

  static info(...args) {
    _Log.log("info", ...args);
  }

  static debug(...args) {
    _Log.log("debug", ...args);
  }
}
