import ApiClient from "../apiClient.js";
import * as constant from "../../common/constant.js";
import { createUUID, _Log } from "../../utils/util.js";
import _ from "lodash";

////////////////////////
/////// Notify API ///////
////////////////////////

const API_PREFIX = "/webapi/sse";
const API_MODULE = "/notify";
const API_VERSION = "/v1";

const API_NAME = API_PREFIX + API_VERSION + API_MODULE;
let __TNT_ID = "";

const apiClient = function () {
  if (typeof ApiClient.instance == "undefined" || ApiClient.instance == null) {
    const initErrorMsg = "Ipron server has not been initialized";
    _Log.error(initErrorMsg);
    throw new Error(initErrorMsg);
  }

  return ApiClient.instance;
};

/**
 *   
 * @param {String} topic
 * @returns EventSource
 */
export function getSubscriptions(topic) {
  return apiClient().eventMap[topic];
}

/**
 *  
 * @param {String} tntId
 * @param {String} topic
 * @param {function|object} eventCallback
 * @param {function} eventErrorCallback
 * @param {String} subscribePath
 */
export function addSubscriptions(tntId, topic, eventCallback, eventErrorCallback, subscribePath) {
  const defaultReq = apiClient().axios.defaults;
  const baseURL = defaultReq.baseURL;
  const headerToken = defaultReq.headers.common.Authorization;
  const headerTokenSplit = headerToken.split(" ");
  const clientId = apiClient().clientId;
  const params = {
    id: createUUID(),
    clientId,
    eventsubject: topic,
    bcloudToken: headerTokenSplit[1],
  };
  const paramsString = apiClient().axios.defaults.paramsSerializer(params);

  __TNT_ID = tntId;
  apiClient()?.isDebug && _Log.debug(`Try connect eventSource [${topic}]`, params);
  subscribePath = subscribePath || topic?.split("/")?.[0] || "undefined";
  apiClient().eventMap[topic] = new EventSource(`${baseURL}${API_NAME}/${tntId}/subscribe/${subscribePath}?${paramsString}`);

  const eventsource = apiClient().eventMap[topic];

  eventsource.addEventListener(constant.IPRON.EVENT.HANDLER.REGISTER, e => {
    const data = JSON.parse(e.data);
    apiClient()?.isDebug && _Log.debug(`${e.type} [${topic}] Event`, data);
    if (!eventCallback || typeof eventCallback !== "object") return;
    eventCallback?.[constant.IPRON.EVENT.HANDLER.REGISTER]?.(data);
  });

  eventsource.addEventListener(constant.IPRON.EVENT.HANDLER.REGISTERED, e => {
    const data = JSON.parse(e.data);
    apiClient()?.isDebug && _Log.debug(`${e.type} [${topic}] Event`, data);
    if (!eventCallback || typeof eventCallback !== "object") return;
    eventCallback?.[constant.IPRON.EVENT.HANDLER.REGISTERED]?.(data);
  });

  eventsource.addEventListener(constant.IPRON.EVENT.HANDLER.PUSH, e => {
    const data = JSON.parse(e.data);
    apiClient()?.isDebug && _Log.debug(`${e.type} [${topic}] Event`, data);
    if (!eventCallback) return;
    else if (typeof eventCallback === "function") eventCallback(data);
    else if (typeof eventCallback === "object") eventCallback?.[constant.IPRON.EVENT.HANDLER.PUSH]?.(data);
  });

  eventsource.addEventListener(constant.IPRON.EVENT.HANDLER.PROBEREQ, e => {
    const data = JSON.parse(e.data);
    apiClient()?.isDebug && _Log.debug(`${e.type} [${topic}] Event`, data);
    if (!eventCallback || typeof eventCallback !== "object") return;
    eventCallback?.[constant.IPRON.EVENT.HANDLER.PROBEREQ]?.(data);
  });

  eventsource.addEventListener(constant.IPRON.EVENT.HANDLER.BANISHMENT, e => {
    const data = JSON.parse(e.data);
    apiClient()?.isDebug && _Log.debug(`${e.type} [${topic}] Event`, data);
    const clientId = apiClient().clientId;
    const oldAppId = data?.data?.oldAppId;
    const isBanished = clientId === oldAppId;
    if (isBanished) delSubscriptions(topic);
    if (!eventCallback || typeof eventCallback !== "object") return;
    eventCallback?.[constant.IPRON.EVENT.HANDLER.BANISHMENT]?.(data, isBanished);
  });

  eventsource.onmessage = e => {
    const data = JSON.parse(e.data);
    apiClient()?.isDebug && _Log.debug(`${e.type} [${topic}] Event`, data);
  };

  eventsource.onerror = async e => {
    _Log.error(`Catch error eventsource [${topic}]`, e);
    delSubscriptions(topic);
    apiClient()?.isDebug && eventErrorCallback && _Log.debug(`Call function eventErrorCallback`);
    eventErrorCallback?.();
  };
  apiClient().eventMap[topic] = eventsource;
}

/**
 *  , topic      
 * @param {String} subs_id
 */
export function delSubscriptions(topic) {
  if (topic) {
    if (apiClient().eventMap[topic]) {
      apiClient().eventMap[topic]?.close();
      delete apiClient().eventMap[topic];
      _Log.info(`Close EventSource [${topic}]`);
    } else {
      _Log.error(`Not found EventSource [${topic}]`);
    }
  } else {
    _Log.warn(`Close EventSource. Topic parameter is empty. All EventSource will be closed.`);
    _.keys(apiClient().eventMap).forEach(topic => delSubscriptions(topic));
    apiClient().eventMap = {};
  }
}

/**
 * user  
 * @param {String} tntId
 * @param {String} userId
 * @param eventCallback
 * @param eventErrorCallback
 */
export function addUserSubscriptions(tntId, userId, eventCallback, eventErrorCallback) {
  const topic = `user/${userId}`;
  this.addSubscriptions(tntId, topic, eventCallback, eventErrorCallback);
}
export function delUserSubscriptions(userId) {
  const topic = `user/${userId}`;
  this.delSubscriptions(topic);
}

